@use '../../../config';
// @use '../../../tokens';

@use '../config' as tplConfig;

@use '../../../lib' as *;

@if config.$print_headings {
    /* # GLOBALS - Forms
     * ======================================================================== */
}

%fieldset {
    @include snippet-fieldset;

    > :first-child {
        margin-block-start: 0;
        margin-block-start: calc(
            #{var-mrg-soft('400')} - #{var-mrg-soft('200')}
        );
    }

    > legend + * {
        margin-block-start: 0;
    }
}

%legend {
    fieldset &,
    fieldset &:first-child {
        @include snippet-heading(7);

        margin-inline: calc(#{var-mrg-soft('300')} * -1);
        margin-block: 0 #{var-mrg-soft('300')};

        padding-inline: #{var-mrg-soft('300')};

        font-style: italic;
    }
}

%_input-base {
    @include snippet-input;

    /// BLOCK ///

    label + &,
    label > & {
        margin-block-start: #{var-mrg-firm('200')};
    }
}

%input {
    @extend %_input-base;
    @include snippet-input-types($includeBase: false);
}

%select {
    @extend %_input-base;
    cursor: pointer;
}

%textarea {
    @extend %_input-base;
    cursor: text;
}

%label {
    @include snippet-label;
}

@if tplConfig.$template_include_buttons {
    @if config.$print_headings {
        /* ## Forms - Buttons ==================================== */
    }

    %button,
    %button-secondary {
        @include snippet-button;
    }

    %button-secondary,
    #{selector.unify('%button', '.button-secondary')} {
    }

    %file-selector-button {
        @include snippet-button;

        & {
            display: initial;
            margin: 0;
            margin-inline-end: #{var-mrg-firm('300')};

            font-size: #{var-fs(normal)};
        }
    }
}
